import { getScriptLoader } from '@bigcommerce/script-loader';

import {
    PaymentStrategyFactory,
    toResolvableModule,
} from '@bigcommerce/checkout-sdk/payment-integration-api';

import createPayPalCommerceIntegrationService from '../create-paypal-commerce-integration-service';
import { PayPalCommerceScriptLoader } from '../index';

import PayPalCommerceGooglePayServicePaymentStrategy from './paypal-commerce-google-pay-service';

const createPayPalCommerceGooglePayPaymentStrategy: PaymentStrategyFactory<
    PayPalCommerceGooglePayServicePaymentStrategy
> = (paymentIntegrationService) =>
    new PayPalCommerceGooglePayServicePaymentStrategy(
        paymentIntegrationService,
        createPayPalCommerceIntegrationService(paymentIntegrationService),
        new PayPalCommerceScriptLoader(getScriptLoader()),
    );

export default toResolvableModule(createPayPalCommerceGooglePayPaymentStrategy, [{ id: 'pp' }]);
