import {
    InvalidArgumentError,
    PaymentIntegrationService,
    PaymentMethod,
} from '@bigcommerce/checkout-sdk/payment-integration-api';

import {
    GooglePayCardDataResponse,
    GooglePayPayPalCommerceInitializationData,
    GooglePayPayPalCommerceGatewayParameters,
    GooglePaySetExternalCheckoutData
} from '../types';

import GooglePayGateway from './google-pay-gateway';
import PayPalCommerceScriptLoader from "../../../paypal-commerce-integration/src/paypal-commerce-script-loader";

type TGooglePayConfig = {
    allowedPaymentMethods: any[],
    apiVersion: number,
    apiVersionMinor: number,
    countryCode: string,
    isEligible: boolean,
    merchantInfo: {
        merchantId: string,
        merchantOrigin: string,
    }
}
export default class GooglePayPaypalCommerceGateway extends GooglePayGateway {
    private paypalGoogleSdk: any;
    private googlepayConfig?: TGooglePayConfig;
    private paymentIntegrationService: PaymentIntegrationService;
    private paymentMethod?: PaymentMethod<GooglePayPayPalCommerceInitializationData>;

    constructor(
        service: PaymentIntegrationService,
        private paypalCommerceScriptLoader: PayPalCommerceScriptLoader,
    ) {
        super('paypalsb', service);
        this.paymentIntegrationService = service;
    }

    async initialize(getPaymentMethod: () => PaymentMethod<GooglePayPayPalCommerceInitializationData>): Promise<void> {
        await super.initialize(getPaymentMethod);

        await this.paymentIntegrationService.loadPaymentMethod('googlepaypaypalcommerce');
        this.paymentMethod = getPaymentMethod();

        await this.getPayPalGooglePaySdk();
        await this.getGooglePayConfig();
    }

    showPaymentSheet() {

    }

    async getGooglePayConfig() {
        if (!this.googlepayConfig) {
            this.googlepayConfig = await this.paypalGoogleSdk.Googlepay().config();
        }

        return this.googlepayConfig;
    }

    async getPayPalGooglePaySdk() {
        if (!this.paypalGoogleSdk) {
            const { clientToken, initializationData } = this.paymentMethod || {};
            const { merchantId, clientId } = initializationData || {};

            if (!merchantId || !clientToken || !clientId) {
                throw new InvalidArgumentError('Unable to initialize payment because merchantId or clientToken or clientId are missing.');
            }
            // const clientToken =
            //     '';
            // const merchantId = '2LFJLWMKXP3DJ';
            // const clientId =
            //     'ARZKyg5GQdV2LmA96fp8KaRLFcTT_sj6r9PKayI-iiO2rRlHq85Pd6KIuv57pE3kv8ZzrN3WM8RE7ReN';
            const paypalGoogleSdk = await this.paypalCommerceScriptLoader.loadPayPalGooglePaySDK(
                clientId,
                merchantId,
                clientToken,
            );

            this.paypalGoogleSdk = paypalGoogleSdk;
        }

        return this.paypalGoogleSdk;
    }

    getPaymentGatewayParameters(): GooglePayPayPalCommerceGatewayParameters {
        if (this.googlepayConfig) {
            const { allowedPaymentMethods } = this.googlepayConfig;
            const { gatewayMerchantId } = allowedPaymentMethods[0].tokenizationSpecification.parameters || {};

            return {
                gateway: this.getGatewayIdentifier(),
                gatewayMerchantId,
            }
        }

        return {
            gateway: this.getGatewayIdentifier(),
        }
    }

    async mapToExternalCheckoutData(
        response: GooglePayCardDataResponse,
    ): Promise<GooglePaySetExternalCheckoutData> {
        const data = await super.mapToExternalCheckoutData(response);

        data.nonce = btoa(data.nonce);

        return data;
    }
}
