import { createFormPoster } from '@bigcommerce/form-poster';
import { createRequestSender } from '@bigcommerce/request-sender';

import {
    PaymentStrategyFactory,
    toResolvableModule,
} from '@bigcommerce/checkout-sdk/payment-integration-api';

import GooglePayPaypalCommerceGateway from '../../gateways/google-pay-paypal-commerce-gateway';
import GooglePayPaymentProcessor from '../../google-pay-payment-processor';
import GooglePayPaymentStrategy from '../../google-pay-payment-strategy';
import createGooglePayScriptLoader from '../create-google-pay-script-loader';
import { PayPalCommerceScriptLoader } from "@bigcommerce/checkout-sdk/paypal-commerce-integration";
import { getScriptLoader } from "@bigcommerce/script-loader";

const createGooglePayPPCPPaymentStrategy: PaymentStrategyFactory<GooglePayPaymentStrategy> = (
    paymentIntegrationService,
) =>
    new GooglePayPaymentStrategy(
        paymentIntegrationService,
        new GooglePayPaymentProcessor(
            createGooglePayScriptLoader(),
            new GooglePayPaypalCommerceGateway(
                paymentIntegrationService,
                new PayPalCommerceScriptLoader(getScriptLoader()),
            ),
            createRequestSender(),
            createFormPoster(),
        ),
    );

export default toResolvableModule(createGooglePayPPCPPaymentStrategy, [{ id: 'googlepaypaypalcommerce' }]);
